.window {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba($color: #000, $alpha: 10.5);
  background-color: #070510;
  // background-color: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(20px);

  header  {
    padding: 0 32px;
    
  }

  button {
    border-radius: 8px;
  }

  > div {
    background-color: #070510;
    // background-color: rgba(255, 255, 255, 0.1);
    max-width: 1300px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 50px;
    margin-bottom: 50px;
    padding: 15px;
    border-radius: 10px;
    overflow-y: auto;
    max-height: calc(100vh - 200px);

    &:before {
      content: "";
      height: 100px;
      width: 50%;
      box-shadow: 0 0 500px 200px #4c436582;
      position: absolute;
      top: -130px;
      left: 0;
      z-index: -1;
    }
  }

  #description {
    resize: vertical;
    height: 180px;
  }

  content {
    display: grid;
    grid-template-rows: auto;
    grid-template-areas:
      "form preview"
      "form status";
  }

  .form {
    grid-area: form;
    padding: 32px;
    form {
      display: flex;
      flex-direction: column;
      gap: 16px;

      label {
        display: block;
        position: absolute;
      }

      fieldset {
        padding: 0;
        border: none;
        box-sizing: border-box;
        display: flex;
        gap: 16px;
        > * {
          flex-grow: 1;
        }

        input,
        textarea {
          background-color: rgba($color: #fff, $alpha: 0.1);
          box-sizing: border-box;
          border: none;
          padding: 16px 8px;
          font-size: 16px;
          width: 100%;
          color: white;
          border-radius: 8px;

          &:focus {
            outline: none;
            background-color: rgba($color: #fff, $alpha: 0.15);
          }
        }
      }
    }
  }

  .preview {
    grid-area: preview;

    article {
      margin: 0 auto;
    }
  }

  .status {
    p {
      width: 300px;
      margin: 0 auto;
    }
  }

  .status {
    grid-area: status;
  }
}
