#search {
  width: 100%;
  margin: 50px 0 80px 0;

  input {
    background-color: rgba($color: #fff, $alpha: 0.1);
    box-sizing: border-box;
    border: none;
    padding: 20px 16px;
    font-size: 20px;
    width: 100%;
    color: white;
    border-radius: 10px;

    &:focus {
      outline: none;
      background-color: rgba($color: #fff, $alpha: 0.15);
    }
  }

  div {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -5px;
    margin-top: 20px;
  }

  #keywords {
    justify-content: right;
    padding: 0 20px;
    margin-top: -50px;
    min-height: 37px;
    margin-bottom: 35px;
  }
}
