.button {
  --font-size: 18px;
  --img-size: 30px;
  --gap: 6px;
  --padding: 8px 12px;
  &.icon {
    --padding: 8px;
    --gap: 0;
  }
  display: inline-block;


  button,
  a {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: var(--gap);
    border: none;
    padding: var(--padding);
    background-color: rgba(255, 255, 255, 0.1);
    text-decoration: none;
    color: white;
    cursor: pointer;

    span {
      font-size: var(--font-size);
    }

    img {
      max-width: var(--img-size);
      max-height: var(--img-size);
    }

    &:not([disabled]):hover {
      background-color: rgba(255, 255, 255, 0.2);

      &:active {
        background-color: rgba(255, 255, 255, 0.3);
      }
    }
  }

  &.disabled {
    button {
      cursor: not-allowed;
    }
  }



  &.simple {
    button {
      background: none;
      color: rgba(255, 255, 255, 0.5);

      &:not([disabled]):hover {
        background: none;
        color: rgba(255, 255, 255, 0.75);
  
        &:active {
          background: none;
          color: rgba(255, 255, 255, 1);
        }
      }

    }

    
  }
}
