#header {
  color: white;
  display: flex;
  justify-content: space-between;
  height: 69px;
  align-items: center;
  margin-top: 21px;

  nav {
    ul {
      display: flex;
    }
  }

  h1 {
    --size: 40px;
    font-size: 22px;
    height: var(--size);
    width: var(--size);
    margin-left: 6px;
  }

  &:before {
    content: "";
    height: 100px;
    width: 50%;
    box-shadow: 0 0 500px 200px #4c436582;
    position: absolute;
    top: -130px;
    left: 0;
    z-index: -1;
  }
}
