@import url("./components/header/header.scss");
@import url("./components/button/button.scss");
@import url("./components/article/article.scss");
@import url("./components/window/window.scss");
@import url("./components/search/search.scss");
@import url("./components/keyword/keyword.scss");
@import url("./components/cat/cat.scss");

body {
  margin: 0;
  padding: 0 20px;
  font-family: -apple-system, "Helvetica Neue", Arial;
  background-color: black;
  color: white;
  height: 100vh;
  box-sizing: border-box;
  background-color: #070510; 
}

h1,
h2,
h3,
p {
  margin: 0;
  font-family: -apple-system, "Helvetica Neue", Arial;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

#root {
  max-width: 1300px;
  height: 100%;
  box-sizing: border-box;
  margin-left: auto;
  margin-right: auto;
  position: relative;
}

main {
  display: flex;
  flex-wrap: wrap;
  gap: 25px;
  margin: 50px 0;
  position: relative;
  justify-content: space-between;
  // background-color: rgba($color: #000, $alpha: .5);
  // border-radius: 5px;
  // overflow: hidden;
}
