article {
  width: 300px;
  background-color: #070510;
  border-radius: 5px;
  height: 460px;
  position: relative;
  overflow: hidden;

  &.hide {
    display: none;
  }

  .border-gradient {
    // --alpha: 0.8;
    --alpha: 0.9;
    height: 100%;
    width: 100%;
    padding: 4px;
    overflow: hidden;
    position: relative;
    box-sizing: border-box;

    > div {
      position: relative;
    }
  }

  .main-gradient {
    height: calc(100% - 8px);
    width: calc(100% - 8px);
    border-radius: 5px;
    overflow: hidden;
    position: absolute !important;
    top: 4px;
    left: 4px;
    background-color: #070510;

    div {
      height: 100%;
      width: 100%;
      --alpha: 0.2;
      position: absolute;
      top: 0;
      left: 0;
    }
  }

  .background {
    border-radius: 3px 3px 0 0;
    width: calc(100% + 50px);
    width: calc(100%);
    height: 140px;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    // mask-image: url(https://media.discordapp.net/attachments/741268256450543618/1045106163861176400/mask2.png);
    mask-image: url(mask.svg);
    mask-position: bottom left;
    mask-repeat: no-repeat;
    mask-size: 100%;

    img {
      object-fit: cover;
      width: 100%;
      height: 100%;
      transition: all 0.2s;
    }
  }

  .description {
    margin: 15px;
    padding: 12px;
    background-color: rgba($color: #000, $alpha: 0.4);
    position: relative;
    border-radius: 5px;
    max-height: 160px;
    overflow: hidden;
  }

  div.keywords {
    margin: 0 10px;
    display: flex;
    flex-wrap: wrap;
    position: absolute;
    bottom: 15px;
  }

  .icon {
    margin: 0 25px;
    img {
      background-color: white;
      border: none;
      border-radius: 9px;
      height: 32px;
      padding: 7px;
      width: 32px;
      margin-top: -29px;
      margin-left: 2px;
    }
  }

  a {
    display: flex;
    color: white;
    text-decoration: none;

    h2 {
      font-size: 18px;
      display: flex;
      align-items: center;
      gap: 10px;
      align-items: end;
      align-items: center;
      margin-bottom: 10px;
      max-width: calc(100% - 21px - 5px);
    }

    svg {
      height: 21px;
      margin: 0;
      margin-top: -2px;
      width: 21px;
      margin-left: 5px;
    }

    &:hover {
      text-decoration: underline;
    }
  }

  p {
    font-size: 16px;
    opacity: 0.75;
    max-height: 200px;
  }

  &:hover {
    .background img {
      transform: scale(1.05);
    }

    .border-gradient {
      --alpha: 1;

      div {
        --alpha: 0.35;
      }
    }
  }
}
